import React from 'react';
import classNames from 'classnames';
import './close_button.css';

export default function CloseButton(props) {
  let closeButtonClasses = classNames({
    [`${props.className}`]: props.className,
    'close-button': true
  });

  return (
    <div onClick={props.closeAction} className={closeButtonClasses}>
      <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 25 31.25"><path fill="#E4509B" d="M17.45 6.843l-4.95 4.95-4.95-4.95-.707.707 4.95 4.95-4.95 4.95.707.707 4.95-4.95 4.95 4.95.706-.708-4.95-4.95 4.95-4.95z" /><path fill="#E4509B" d="M12.5 0C5.596 0 0 5.596 0 12.5 0 19.403 5.596 25 12.5 25S25 19.403 25 12.5C25 5.596 19.404 0 12.5 0zm0 24C6.172 24 1 18.828 1 12.5 1 6.17 6.172 1 12.5 1S24 6.17 24 12.5C24 18.828 18.828 24 12.5 24z" /></svg>
    </div>
  );
}


